@import url(https://fonts.googleapis.com/css?family=Montserrat);
html {
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  padding-top: 4.5rem;
}

.cover-photo {
  width: 100%;
  height: 100%;
}

.bg-light {
  background-color: white !important;
  box-shadow: 0 1px 10px #bcbcbc;
}

.container-fluid {
  padding: 0 !important;
}

.navbar-toggler {
  border: none;
}

.navbar-nav {
  width: 100% !important;
}

.collapse .nav-item {
  width: 100% !important;
  text-align: center !important;
}

.collapsing .nav-item {
  width: 100% !important;
  text-align: center !important;
}

.nav-link {
  line-height: 48px;
}

#navbar .navbar-nav .nav-item.active span {
  border-bottom: 3px solid #6c757d;
  padding-bottom: 8px;
}

footer {
  padding: 30px;
  background-color: #f5f5f5;
}

.footer-left,
.footer-center {
  line-height: 48px !important;
}

.footer-right {
  line-height: 48px !important;
  font-size: 24px;
  margin-left: 15px;
  margin-right: 15px;
  color: black !important;
}

.linkedin:hover {
  color: #0077b5 !important;
}

.github:hover {
  color: #24292e !important;
}

.twitter:hover {
  color: #1da1f2 !important;
}

.mail-link, .link {
  color: black;
  font-weight: bold;
  text-decoration: underline;
  transition: 0.3s;
}

.mail-link:hover, .link:hover {
  color: black;
  font-weight: bold;
  text-decoration: underline;
  opacity: 0.5;
}

.form-control {
  border-radius: 0 !important;
}

.form-control:focus {
  border-color: #737373;
  box-shadow: inset 0 1px 1px #737373, 0 1px 1px #737373;
}

.form-group {
  padding-top: 10px;
  padding-bottom: 10px;
}

.section {
  padding: 50px;
  position: relative;
  overflow: auto;
  min-height: 600px;
}

.section-body {
  position: relative;
  z-index: 2;
}

.section-title {
  text-align: center !important;
  margin-bottom: 20px;
  color: #3c4146;
  font-weight: bold;
  position: relative;
  z-index: 2;
}

.section-title-underline {
  width: 80px !important;
  border: 3px solid #3c4146;
  margin-bottom: 50px;
}

.section-separator-after:after {
  bottom: 0;
  background-position: bottom;
  height: 15%;
  content: "";
  z-index: 1;
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  background-image: url(/portfolio/static/media/dots.da10a58f.svg);
  background-repeat: repeat;
  background-size: 48px 48px;
}

.section-separator-before {
  padding-top: 100px !important;
}

.section-separator-before:before {
  content: "";
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  background-image: url(/portfolio/static/media/dots.da10a58f.svg);
  background-repeat: repeat;
  background-size: 48px 48px;
  top: 0;
  background-position: top;
  height: 38px;
}

.btn-outline-dark {
  background-color: white;
}

.btn-outline-dark:hover {
  background-color: #343a40;
}

.section-gray {
  background-color: #f5f5f5;
}

#projects-nav,
#skills-nav {
  border: none !important;
  background-color: transparent !important;
}

#projects-nav .nav-item,
#skills-nav .nav-item {
  width: 20% !important;
  text-align: center;
  position: relative;
  color: #b0b0b0 !important;
  background-color: transparent !important;
  border: none !important;
}

#projects-nav .nav-item.active,
#skills-nav .nav-item.active {
  color: #6c757d !important;
}

#projects-nav .nav-item.active span,
#skills-nav .nav-item.active span {
  border-bottom: 3px solid #6c757d !important;
  padding-bottom: 8px;
}

#projects-body,
#skills-body {
  padding-top: 50px;
}

.active-arrow {
  position: absolute;
  left: 50%;
  bottom: 0;
  font-size: 20px;
  width: 20px;
  margin-left: -12px;
  margin-bottom: -10px;
}

.card {
  border: 2px solid #6c757d;
}

.card-text {
  color: #858d94;
  font-size: 14px;
  word-spacing: 1px;
  line-height: 24px;
}

.card-subtitle {
  font-size: 16px !important;
}

.skill-logo {
  color: black;
  margin-right: 12px;
}

.skill-img {
  height: 28px;
  margin-right: 12px;
  margin-top: -6px;
}

.post,
.post:hover {
  height: 70px !important;
  color: inherit;
  text-decoration: none;
}

.post-name,
.post-date {
  font-size: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis !important;
}

.see-post,
.see-post:hover {
  color: inherit;
  text-decoration: underline;
  font-weight: bold;
  font-size: 16px;
}

.post-duration {
  color: #858d94;
  font-size: 14px;
  margin-left: 5px;
}

.post-duration::before {
  content: "-";
  color: #858d94;
  font-size: 14px;
  margin-right: 5px;
}
.spinner-wrapper {
  width: 100%;
  position: absolute;
  top: 50%;
  margin-top: -100px;
}

.spinner {
  width: 100px;
}

/*CSS classes to override bootstrap*/

.sharp-corner {
  border-radius: 0 !important;
}

@media (min-width: 576px) {
  .card-columns {
    -webkit-column-count: 1;
            column-count: 1;
  }
}

@media (min-width: 768px) {
  .card-columns {
    -webkit-column-count: 2;
            column-count: 2;
  }
}

@media (min-width: 992px) {
  .card-columns {
    -webkit-column-count: 3;
            column-count: 3;
  }
}

#article-read-header {
  margin-top: -4.5rem;
}

#article-read-header .navbar {
  box-shadow: none !important;
}

#article-read-header .nav-item-left {
  text-align: left !important;
}

#article-read-header .nav-item-right {
  text-align: right !important;
}

#article-read-body .description {
  padding: 15px;
  background-color: #f5f5f5;
  border-left: 5px solid #adadad;
}
